.transaction-history {
	margin: 0;
	padding: 5px;

	width: 500px;

	font-size: 16px;
	font-weight: normal;
}

.table-column--type {
	padding: 0 0 0 15px;
	width: 60%;
}
.table-column--amount {
	padding: 0 10px 0;
	width: 20%;
	text-align: right;
}
.table-column--currency {
	width: 20%;
	text-align: center;
}

.table-head th {
	padding: 5px;
	font-size: 16px;
	font-weight: bold;
	text-transform: uppercase;
	background-color: #009999;
	color: #ffffff;
}

.table-body {
	font-size: 16px;
	font-weight: normal;
	color: #555555;
	background-color: #ffffff;
}

.table-datarow:nth-child(2n) {
	background-color: #e8eeee;
}
